.cart-page__navigation {
  background-color: #f8f8f8;
  margin-bottom: 32px;
}
.cart-page__picture-main-wrap {
  margin-bottom: 94px;
}

.womens-cart-h__title {
  padding-top: 8px;
  padding-bottom: 6px;
}

.womens-cart-h__title-link {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.womens-cart-h__title-item {
  display: flex;
  align-items: center;
}

.womens-cart-h__title-text {
  font-weight: 500;
  font-size: 11px;
  line-height: 18px;
  opacity: 0.6;
}

.womens-cart-h__women-link {
  margin-right: 6px;
}
.womens-cart-h__home-link,
.womens-cart-h__title-arrow {
  margin-right: 7px;
}
.womens-cart-h__share-link {
  margin-left: 9px;
}

.womens-cart-h__title-block {
  margin-top: 18px;
  text-align: center;
}
.womens-cart-h__title-women {
  font-weight: 600;
  font-size: 26px;
  line-height: 36px;
  letter-spacing: 0.06em;
}

.womens-cart-h__title-link_item {
  opacity: 1;
}
.womens-h__share-link {
  margin-left: 9px;
}

.womens-cart-h__star-code {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.womens-cart-h__star {
  display: flex;
  align-items: flex-start;
}
.womens-cart-h__code {
  display: flex;
  align-items: center;
}

.womens-cart-h__star-text {
  margin-left: 4px;
  font-weight: 500;
  font-size: 11px;
  line-height: 18px;
  opacity: 0.6;
}

.womens-cart-h__code-text {
  font-weight: 500;
  font-size: 11px;
  line-height: 18px;
  opacity: 0.6;
  color: #121212;
}

.womens-cart-h__code-text_span {
  font-weight: 700;
  font-size: 11px;
  line-height: 18px;
  opacity: 1;
}

.womens-cart-h__code-text_avail {
  margin-left: 28px;
}

.cart-page__picture-main {
  display: flex;
  justify-content: space-between;
}
.cart-page__picture-img {
  width: 51%;
}
.cart-page__picture-img-block {
  display: flex;
}
.cart-page__picture-img-small {
  margin-right: 14px;
}

.cart-page__picture-img-btns {
  display: flex;
  justify-content: space-around;
  margin-bottom: 15px;
}

.btn__small-left,
.btn__small-right {
  background: transparent;
  border: none;
  cursor: pointer;
}

.cart__img-big {
  width: 448px;
}

.btn__small-left {
  margin-left: -6px;
}
.btn__small-right {
  margin-right: -5px;
}

.btn__small-left-img {
  transform: rotate(90deg);
}
.btn__small-right-img {
  transform: rotate(90deg);
}

.cart__img-small {
  margin-bottom: 16px;
}

.cart-page__picture-img-big {
  position: relative;
}

.btn__big-left,
.btn__big-right {
  position: absolute;
  top: 48.5%;
  transform: translateY(-48.5%);
  cursor: pointer;
  background: transparent;
}

.btn__big-left {
  left: 17px;
}
.btn__big-right {
  right: 14px;
}

.cart-page__picture-text {
  width: 47%;
}

.cart-page__picture-color {
  display: flex;
  padding-top: 3px;
  margin-bottom: 16px;
}

.cart-page__picture-color_text {
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  opacity: 0.6;
  margin-right: 5px;
}

.cart-page__color-span {
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  text-transform: capitalize;
}

.cart-page__picture-mini-img {
  display: flex;
  margin-bottom: 32px;
}
.mini-img {
  margin-right: 17px;
  width: 64px;
  height: 64px;
}

.mini-img.active-mini {
  border: 3px solid #121212;
}

.cart-page__picture-size {
  display: flex;
  margin-bottom: 16px;
}

.cart-page__picture-size_text {
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  opacity: 0.6;
  margin-right: 6px;
}

.cart-page__size-span {
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  text-transform: uppercase;
}

.cart-page__picture-size-block {
  display: flex;
  margin-bottom: 16px;
}

.cart-page__picture-size-item {
  width: 40px;
  height: 40px;
  display: inline-block;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f8f8f8;
  margin-right: 17px;
}

.cart-page__picture-size-item p {
  display: inline-block;
  font-weight: 600;
  font-size: 12px;
  line-height: 13px;
  color: #121212;
  opacity: 0.7;
  text-transform: uppercase;
}

.cart-page__picture-size-item.active-size {
  border: 3px solid #121212;
}

.cart-page__picture-size-item.active-size p {
  opacity: 1;
}

.cart-page__picture-size-item:hover {
  border: 3px solid #121212;
  cursor: pointer;
}

.cart-page__hanger {
  display: flex;
  align-items: flex-start;
  margin-bottom: 34px;
}
.hanger-img {
  margin-left: 3px;
  margin-right: 9px;
}
.cart-page__hanger-text {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  opacity: 0.6;
}

.cart-page__price {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 32px 0;
  border-bottom: 2px solid #e5e5e5;
  border-top: 2px solid #e5e5e5;
  margin-bottom: 36px;
}

.cart-page__price-number {
  font-weight: 700;
  font-size: 22px;
  line-height: 36px;
  text-transform: uppercase;
  margin-right: 26px;
}

.cart-page__price-btn {
  font-weight: 500;
  font-size: 13px;
  line-height: 15px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #ffffff;
  opacity: 0.9;
  background-color: #121212;
  padding: 16px 24px;
  margin-right: 30px;
  cursor: pointer;
}

.cart-page__price-heart {
  margin-right: 43px;
  cursor: pointer;
}

.cart-page__info {
  display: flex;
  align-items: center;
  margin-bottom: 40px;
}

.cart-page__info-item {
  display: flex;
  align-items: flex-end;
}

.cart-page__info-text {
  font-weight: 500;
  font-size: 12px;
  opacity: 0.6;
}
.cart-page__info-img {
  margin-right: 10px;
}

.cart-page__info-car {
  margin-right: 26px;
}
.cart-page__info-refresh {
  margin-right: 54px;
}

.cart-page__creditcards {
  position: relative;
  border-top: 2px solid #e5e5e5;
  border-bottom: 2px solid #e5e5e5;
  padding: 32px 0;
}

.cart-page__creditcards-item {
  display: flex;
}
.cart-page__creditcards-img {
  margin-right: 16px;
}
.cart-page__creditcards-img:last-child {
  margin-right: 0;
}

.cart-page__creditcards-text {
  display: inline-block;
  position: absolute;
  top: -8px;
  left: 0;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  background-color: #fff;
  padding-right: 24px;
}

.cart-page__description {
  border-bottom: 2px solid #e5e5e5;
  padding: 32px 0;
  margin-bottom: 10px;
}

.cart-page__description-text {
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  opacity: 0.6;
}
.cart-page__information {
  padding: 22px 0;
  border-bottom: 2px solid #e5e5e5;
  margin-bottom: 31px;
}

.cart-page__information-item {
  margin-bottom: 12px;
}
.cart-page__information-title {
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  margin-bottom: 32px;
}

.cart-page__information-title_span {
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  text-transform: capitalize;
  margin-right: 5px;
}

.cart-page__title_percent {
  margin-right: 9px;
}

.cart-page__information-text_span {
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  opacity: 0.6;
  text-transform: capitalize;
  margin-right: 4px;
}
.cart-page__span-text {
  text-transform: uppercase;
}

.cart-page__reviews {
  border-bottom: 2px solid #e5e5e5;
  padding-bottom: 31px;
}

.cart-page__reviews-title {
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  margin-bottom: 33px;
}

.cart-page__reviews-stars-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 27px;
}

.cart-page__reviews-stars {
  display: flex;
  align-items: center;
}

.cart-page__stars-img {
  display: flex;
  align-items: center;
  padding-left: 3px;
  margin-right: 5px;
}
.cart-page__reviews-img {
  margin-right: 4px;
}

.cart-page__stars-text {
  font-weight: 500;
  font-size: 11px;
  line-height: 18px;
  opacity: 0.6;
  text-transform: capitalize;
}

.cart-page__reviews-write-block {
  display: flex;
  align-items: center;
}

.write-text {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  opacity: 0.6;
  margin-left: 5px;
}

.cart-page__author-block-star {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.cart-page__reviews-author {
  margin-bottom: 48px;
}
.cart-page__reviews-author:last-child {
  margin-bottom: 0;
}
.cart-page__author {
  margin-bottom: 12px;
}
.cart-page__author-name {
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
}
.cart-page__author-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  opacity: 0.8;
}

.cart-page__product {
    margin-bottom: 95px;
}
.cart-page__product-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 38px;
}

.cart-page__product-title {
  font-weight: 600;
  font-size: 22px;
  line-height: 25px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
}

.btn-product__small-left,
.btn-product__small-right {
  background: transparent;
}
.btn-product__small-right {
  margin-left: 15px;
}

.womens-cart-block {
  display: flex;
  justify-content: space-between;
}

.womens-cart__stars-text_sale {
  font-weight: bold;
  font-size: 12px;
  line-height: 13px;
  text-decoration: line-through;
  opacity: 0.4;
  color: #121212;
}

@media screen and (max-width: 500px) {
    .womens-cart-h__title-women {
        font-size: 23px;
    }
  }

